import styled from "styled-components";
import replacement from "./data/replacement.json";
import data from "./data/news.json";

const StyledSection = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const StyledItem = styled.blockquote`
  border: 2px dashed darkgrey;
  padding: 20px;
  text-align: center;
`;

const StyledProjectExplainer = styled.div`
  color: #666;
`;

const StyledTitle = styled.h1`
  font-size: 1.5em;
  margin-bottom: 0.25em;
  margin-top: 1.5em;
`;

const StyledSource = styled.cite`
  color: grey;
  display: flex;
  font-size: 1.2em;
  justify-content: end;
  margin-right: 1em;
  margin-bottom: 1.5em;
`;

const StyledLink = styled.a`
  text-decoration: none;
  color: #222;
`;

const StyledKeyboard = styled.div`
  color: darkgrey;
  font-size: 1.5em;
  margin-top: 4em;
`;

export const News = () => {
  const discriminate =
    replacement[Math.floor(Math.random() * replacement.length)];
  const item = data[Math.floor(Math.random() * data.length)];
  const title = item["title"].replace(item.replace, discriminate);

  return (
    <StyledSection>
      <StyledProjectExplainer>
        Diskriminierung war noch nie akzeptabel. Mal sehen, wie es klingt, wenn
        man <strong>{item.replace}</strong> mit <strong>{discriminate}</strong>{" "}
        ersetzt.
      </StyledProjectExplainer>
      <StyledItem>
        <StyledTitle>
          <StyledLink
            href={item.url}
            target="_blank"
            rel="noopener noreferrer nofollow"
          >
            »{title}«
          </StyledLink>
        </StyledTitle>
        <StyledSource>{item.source}</StyledSource>
      </StyledItem>
      <StyledProjectExplainer>
        Doch irgendwie nicht so geil, richtig? Vielleicht hören wir dann mal
        alle auf gegen eine Gruppe von Menschen zu Stimmung zu machen?
      </StyledProjectExplainer>
      <StyledKeyboard>Seite neuladen für neue Diskriminierung.</StyledKeyboard>
    </StyledSection>
  );
};
